<template>
    <div class="shapes-component">
        <div class="div demoWrapper">
            <div class="div">
                <v-stage :config="configKonva">
                    <v-layer>
                        <v-circle :config="configCircle"></v-circle>
                        <v-rect :config="configRect"></v-rect>
                        <v-star :config="configStar"></v-star>
                        <v-rect :config="configSquare"></v-rect>
                    </v-layer>
                </v-stage>
            </div>
        </div>
        <div class="row overflow-hidden">
            
        </div>
    </div>
</template>
<script>

export default {
    name: 'ShapesComponent',
    data() {
        return{
            configKonva: {
                width: 500,
                height: 400
            },
            configCircle: {
                x: 50,
                y: 50,
                radius: 50,
                fill: "#ccc",
            },
            configRect: {
                x: 120,
                y: 10,
                width: 120,
                height: 80,
                fill: "#ccc",
            },
            configStar: {
                numPoints: 5,
                innerRadius: 20,
                outerRadius: 50,
                x: 320,
                y: 50,
                fill: "#ccc",
            },
            configSquare: {
                x: 400,
                y: 10,
                width: 80,
                height: 80,
                fill: "#ccc",
            }
        }
    }
}
</script>
<style>
.demoWrapper {
  padding: 10px;
  background: white;
  box-sizing: border-box;
  resize: horizontal;
  /* border: 1px dashed; */
  overflow: auto;
  max-width: 100%;
  height: calc(20vh);
}

.div {
  width: 100%;
  padding-bottom: 75%;
}

@media (min-width: 1400px){
    .demoWrapper {
        padding: 10px;
        background: white;
        box-sizing: border-box;
        resize: horizontal;
        /* border: 1px dashed; */
        overflow: hidden;
        max-width: 100%;
        height: 100%;
    }
}
</style>