<template>
    <div class="settings">
        <div class="row">
            <div class="col-12">
                <div class="settings-wrapper">
                    <div class="settings-header text-center py-5 mb-sm-4">
                        <h3 class="settings-title">قم بتحديد القسم أولا ثم قم باختيار الشكل</h3>
                    </div>
                    <div class="settings-body m-3 shadow m-auto shadow overflow-hidden">
                        <div class="links">
                            <div class="row">
                                <div class="col-3 p-3 text-center" ref="pics" v-on:click="pics()">
                                    <h5>صور</h5>
                                </div>
                                <div class="col-3 p-3 text-center" ref="text" v-on:click="text()">
                                    <h5>كتابات</h5>
                                </div>
                                <div class="col-3 p-3 text-center" ref="shapes" v-on:click="shapes()">
                                    <h5>أشكال</h5>
                                </div>
                                <div class="col-3 p-3 text-center active" ref="backg" v-on:click="backg()">
                                    <h5>خلفيات</h5>
                                </div>
                            </div>
                        </div>
                        <div class="settings-content p-3">
                            <BckgroundsComponent ref="BckgroundsComponent" :class=" BckgroundsComponent ? 'd-block' :  'd-none' "/>
                            <ShapesComponent ref="ShapesComponent" :class=" ShapesComponent ? 'd-block' :  'd-none' "/>
                            <TextComponent ref="TextComponent" :class=" TextComponent ? 'd-block' :  'd-none' "/>
                            <PicsComponent ref="PicsComponent" :class=" PicsComponent ? 'd-block' :  'd-none' "/>
                        </div>
                    </div>
                </div>
            </div>
            <div class="col-12 pt-5 d-flex flex-row-reverse align-items-center">
                <h5 class="color-title mx-3">: الألوان</h5>
                <div>
                    <v-input-colorpicker />
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import BckgroundsComponent from './settings-content/BckgroundsComponent'
import PicsComponent from './settings-content/PicsComponent'
import ShapesComponent from './settings-content/ShapesComponent'
import TextComponent from './settings-content/TextComponent'

export default {
    name: 'Settings',
    data(){
        return{
            BckgroundsComponent : true,
            PicsComponent : false,
            ShapesComponent : false,
            TextComponent : false
        }
    },
    components: {
        BckgroundsComponent,
        PicsComponent,
        ShapesComponent,
        TextComponent
    },
    methods:{
        pics(){
            this.$refs.pics.classList.add('active');
            this.$refs.text.classList.remove("active");
            this.$refs.shapes.classList.remove("active");
            this.$refs.backg.classList.remove("active");

            this.PicsComponent = true;
            this.BckgroundsComponent = false;
            this.ShapesComponent = false;
            this.TextComponent = false;
            
        },
        text(){
            this.$refs.text.classList.add('active');
            this.$refs.pics.classList.remove("active");
            this.$refs.shapes.classList.remove("active");
            this.$refs.backg.classList.remove("active");

            this.PicsComponent = false;
            this.BckgroundsComponent = false;
            this.ShapesComponent = false;
            this.TextComponent = true;
        },
        shapes(){
            this.$refs.shapes.classList.add('active');
            this.$refs.pics.classList.remove("active");
            this.$refs.text.classList.remove("active");
            this.$refs.backg.classList.remove("active");

            this.PicsComponent = false;
            this.BckgroundsComponent = false;
            this.ShapesComponent = true;
            this.TextComponent = false;
        },
        backg(){
            this.$refs.backg.classList.add('active');
            this.$refs.pics.classList.remove("active");
            this.$refs.text.classList.remove("active");
            this.$refs.shapes.classList.remove("active");

            this.PicsComponent = false;
            this.BckgroundsComponent = true;
            this.ShapesComponent = false;
            this.TextComponent = false;
        },
    }
}
</script>
<style>
    .settings-body{
        width: 100%;
        border: 1px solid #ccc ;
        border-radius: 20px;
        height: 500px;
    }
   .links .row{
        background-color: #ccc;
        margin-right: 0px;
        margin-left: 0px;
   }
   .links .row .col-3 {
       cursor: pointer;
   }
   .active {
       background-color: #dee2e6;
   }
   .icp__input {
       border: 2px solid #ccc !important;
       height: 50px !important;
       border-radius: 15px !important;
       width: 200px !important;
   }
   
</style>