<template>
    <div class="display">
        <div class="row">
            <div class="col-12">
                <div class="pad-wrapper">
                    <div class="pad-header text-center py-5">
                        <h3 class="pad-title">قم بتحديد الشكل بإستخدام الماوس أولا ثم قم بالتعديل من الأدوات الموجودة بالأسفل</h3>
                    </div>
                    <div class="pad m-3 shadow m-auto overflow-hidden">
                        <v-stage @click="stageClick" ref='stage' :config="configKonva">
                            <v-layer>
                                <v-circle :config="configCircle"
                                          @mouseenter="handleMouseEnter"
                                          @mouseleave="handleMouseLeave"
                                ></v-circle>
                                <v-rect :config="configRect"
                                        @mouseenter="handleMouseEnter"
                                        @mouseleave="handleMouseLeave"
                                        @click="rectClick" ref='rect' 
                                ></v-rect>
                                <v-star :config="configStar"
                                        @mouseenter="handleMouseEnter"
                                        @mouseleave="handleMouseLeave"    
                                ></v-star>
                                <v-rect :config="configSquare"
                                        @mouseenter="handleMouseEnter"
                                        @mouseleave="handleMouseLeave"
                                ></v-rect>
                            </v-layer>
                        </v-stage>
                    </div>
                </div>
            </div>
            <div class="col-12">
                <div class="pad-settings pt-5">
                    <div class="row">
                        <div class="col-3">
                            <div class="zoom">
                                <div class="icon-wrapper d-flex justify-content-center align-items-center">
                                    <span class="icon m-md-3 h2"><i class="fas fa-search-plus"></i></span>
                                    <span class="icon m-md-3 h2"><i class="fas fa-search-minus"></i></span>
                                </div>
                                <h5 class="m-auto text-center">التكبير والتصغير</h5>
                            </div>
                        </div>
                        <div class="col-3">
                            <div class="zoom">
                                <div class="icon-wrapper d-flex justify-content-center align-items-center">
                                    <span class="icon m-md-3 h2"><i class="fas fa-undo"></i></span>
                                </div>
                                <h5 class="m-auto text-center">الدوران</h5>
                            </div>
                        </div>
                        <div class="col-3">
                            <div class="zoom">
                                <div class="icon-wrapper d-flex justify-content-center align-items-center">
                                    <span class="icon m-md-3 h2"><i class="fas fa-circle"></i></span>
                                    <span class="icon m-md-3 h2"><i class="far fa-circle"></i></span>
                                </div>
                                <h5 class="m-auto text-center">الشفافية</h5>
                            </div>
                        </div>
                        <div class="col-3">
                            <div class="zoom">
                                <div class="icon-wrapper d-flex justify-content-center align-items-center">
                                    <span class="icon m-md-3 h2"><i class="fas fa-recycle"></i></span>
                                </div>
                                <h5 class="m-auto text-center">اعادة التعديل</h5>
                            </div>
                        </div>
                    </div>
                    
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'Display',
    data(){
        return {
            configKonva: {
                width: 330,
                height: 500
            },
            configCircle: {
                x: 165,
                y: 250,
                radius: 50,
                fill: "#ccc",
                draggable: true,
            },
            configRect: {
                x: 165,
                y: 250,
                width: 120,
                height: 80,
                fill: "#ccc",
                draggable: true,
            },
            configStar: {
                numPoints: 5,
                innerRadius: 20,
                outerRadius: 50,
                x: 165,
                y: 250,
                fill: "#ccc",
                draggable: true,
            },
            configSquare: {
                x: 165,
                y: 250,
                width: 80,
                height: 80,
                fill: "#ccc",
                draggable: true,
            }
        }
    },
    methods: {
        handleMouseEnter(e) {
            const stage = e.target.getStage();
            stage.container().style.cursor = "pointer";
        },
        handleMouseLeave(e) {
            const stage = e.target.getStage();
            stage.container().style.cursor = "default";
        },
        stageClick (comp, e) {
            let stage = this.$refs.stage.getStage()
            // if click on empty area - remove all transformers
            if (e.target === stage) {
                stage.find('Transformer').destroy();
                stage.draw();
                return;
            }
        },
        rectClick (comp, e) {
            let stage = this.$refs.stage.getStage()
            stage.find('Transformer').destroy()
            // create new transformer
            var tr = new window.Konva.Transformer()
            let layer = this.$refs.layer.getStage(tr)
            tr.attachTo(e.target) // no idea what to do here ...
            layer.add(tr);
            layer.draw()
        }
    },
}
</script>

<style>

.display {
    height: 100%;
}
.pad {
    height: 500px;
    width: 330px;
    border: 1px solid #ccc;
    overflow: hidden;
}
.pad-title{
    max-width: 480px;
    margin: 0 auto;
}  
.icon{
    cursor: pointer;
}

@media (max-width: 768px) {
    .icon{
        padding: 0px 5px;
    }
}


</style>